.Nav {
    text-align: center;
  }
  
  .Nav-logo {
    -webkit-animation: Nav-logo-spin infinite 20s linear;
            animation: Nav-logo-spin infinite 20s linear;
    height: 50px;
  }
  
  .Nav-header {
    background-color: #222;
    height: 75px;
    padding: 5px;
    color: grey;
   
  }
  
  .Nav-title {
    font-size: 1em;
    color: white;
  }
  
  .Nav-intro {
    font-size: large;
  }
  
  @-webkit-keyframes Nav-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes Nav-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  


